export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Virtual Items</h1>\r\n      <p>UI for face masks items and grid.</p>\r\n\r\n      <pre><code>import { ItemGrid, VirtualItem } from \"streamlabs-beaker\"\r\n\r\ncomponents: {\r\n  ItemGrid,\r\n  VirtualItem\r\n}</code></pre>\r\n\r\n      <div class=\"section\">\r\n        <h2>Giveaway Items</h2>\r\n        <DemoSection title=\"Giveaway Items\" :code=\"demoCode\">\r\n          <template #components>\r\n            <div class=\"container\">\r\n              <VirtualItem\r\n                preview=\"https://streamlabs.com/imgs/facemasks/robot-mask.png\"\r\n                rarity=\"Enter Giveaway\"\r\n                name=\"4x VIP Tickets to PAX, TwitchCon and SXSW\"\r\n                remainingTime=\"2h left\"\r\n                :hasWarning=\"true\"\r\n              />\r\n\r\n              <VirtualItem\r\n                preview=\"https://streamlabs.com/imgs/facemasks/robot-mask.png\"\r\n                rarity=\"Entered\"\r\n                name=\"$5K Custom High-Spec Streaming Desktop PC\"\r\n                remainingTime=\"5d left\"\r\n                :isGiveaway=\"true\"\r\n              />\r\n            </div>\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <h2>Face Masks</h2>\r\n      <DemoSection title=\"Face Masks\" :code=\"demoCode\">\r\n        <template #components>\r\n          <ItemGrid>\r\n            <VirtualItem\r\n              v-for=\"(mask, id) in masks\"\r\n              :key=\"id\"\r\n              :preview=\"mask.preview\"\r\n              :rarity=\"mask.rarity\"\r\n              :name=\"mask.name\"\r\n              :selectionCount=\"mask.selectionCount\"\r\n              :selected=\"mask.selected\"\r\n              @click=\"clickEvent\"\r\n            />\r\n          </ItemGrid>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <table class=\"docs-table\">\r\n      <thead>\r\n        <tr>\r\n          <th>Props</th>\r\n          <th>Type</th>\r\n          <th>Default</th>\r\n          <th>Description</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr>\r\n          <td>name</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>Name of the item.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>value</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>Value you want to assign to the item.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>preview</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>Item preview image.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>quantity</td>\r\n          <td>number</td>\r\n          <td>null</td>\r\n          <td>Quantity of the particular item.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>rarity</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>\r\n            Gives item rarity class <code>s-virtual-item--${rarity}</code> and\r\n            adds rarity string to item. Use for giveaway text.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>selected</td>\r\n          <td>boolean</td>\r\n          <td>false</td>\r\n          <td>Sets item to selected state.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>selectionCount</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>\r\n            Adds counter to top right of item. You have to set this\r\n            functionality.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>remainingTime</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>Remaining time for giveaways or special promotions</td>\r\n        </tr>\r\n        <tr>\r\n          <td>hasWarning</td>\r\n          <td>boolean</td>\r\n          <td>false</td>\r\n          <td>Changes item background color to red.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>isGiveaway</td>\r\n          <td>boolean</td>\r\n          <td>false</td>\r\n          <td>Adds checkmark and set rarity color to teal.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>type</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>Add type text to top of item</td>\r\n        </tr>\r\n      </tbody>\r\n    </table>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport ItemGrid from \"./../components/ItemGrid.vue\";\r\nimport VirtualItem from \"./../components/VirtualItem.vue\";\r\nimport VirtualItemsCode from \"!!raw-loader!./VirtualItems.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    ItemGrid,\r\n    VirtualItem\r\n  }\r\n})\r\nexport default class VirtualItems extends Vue {\r\n  demoCode = VirtualItemsCode;\r\n  masks = [\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\",\r\n      selectionCount: \"1\",\r\n      selected: true\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\",\r\n      selectionCount: \"2\",\r\n      selected: true\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\",\r\n      selectionCount: \"3\",\r\n      selected: true\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\"\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\"\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\"\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\"\r\n    },\r\n    {\r\n      preview: \"https://streamlabs.com/imgs/facemasks/robot-mask.png\",\r\n      name: \"Robot\",\r\n      rarity: \"Common\"\r\n    }\r\n  ];\r\n\r\n  clickEvent() {\r\n    console.log(\"Hey you clicked me!\");\r\n  }\r\n}\r\n</script>\r\n\r\n<style lang=\"less\" scoped>\r\n.container {\r\n  display: grid;\r\n  grid-template-columns: 240px 1fr;\r\n}\r\n</style>\r\n";