



































































import { Component, Prop, Vue } from "vue-property-decorator";
import { Chrome } from "vue-color";
import { cloneDeep } from "lodash-es";

interface ColorOptions {
  hex: string;
  hex8: string;
  hsl: { h: number; s: number; l: number; a: number };
  hsv: { h: number; s: number; v: number; a: number };
  oldHue: number;
  rgba: { r: number; g: number; b: number; a: number };
  a: number;
  source: string;
}

@Component({
  inheritAttrs: false,
  components: {
    picker: Chrome
  }
})
export default class ColorPicker extends Vue {
  $refs!: {
    colorpicker: HTMLElement;
  };

  @Prop()
  value!: any;

  @Prop({ default: "#31c3a2" })
  placeholder!: string;

  @Prop({ default: false })
  hasAlpha!: boolean;

  @Prop({ default: false })
  isMini!: boolean;

  @Prop()
  icon!: string;

  @Prop()
  error!: string;

  @Prop({ default: false })
  allColorTypes!: boolean;

  private displayPicker: Boolean = false;
  private backgroundColor: String = "";

  colors: object = {};

  get alphaClass() {
    return this.hasAlpha
      ? this.colors["a"] === 1
        ? "nonAlpha"
        : "alpha"
      : false;
  }

  get listeners() {
    const { input, ...listeners } = this.$listeners;
    return listeners;
  }

  created() {
    this.colors = Object.assign({}, this.colors, {
      hex: this.value
    });
  }

  updateFromPicker(value: ColorOptions) {
    if (!this.allColorTypes) {
      if (this.alphaClass === "alpha") {
        this.$emit("input", value.hex8);
      } else {
        this.$emit("input", value.hex);
      }
    } else {
      this.$emit("input", value);
    }
  }

  updateFromInput(event: any) {
    this.colors = event.target.value;

    this.$nextTick(() => {
      const emitColor = cloneDeep(this.$refs["chrome-color-picker"].val);
      this.$emit("input", { ...emitColor, hex: this.colors });
    });
  }

  hidePicker() {
    document.removeEventListener("click", this.documentClick);
    this.displayPicker = false;
  }

  showPicker() {
    document.addEventListener("click", this.documentClick);
    this.displayPicker = true;
  }

  documentClick(e: any) {
    let el = this.$refs.colorpicker;
    let target = e.target;
    if (el && el !== target && !el.contains(target)) {
      this.hidePicker();
    }
  }
}
