    </tr>\r\n          <tr>\r\n            <td>desc</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>null</td>\r\n            <td>Description text below banner title.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>linkDesc</td>\r\n            <td>string</td>\r\n            <td>false</td>\r\n            <td>null</td>\r\n            <td>Description text below link.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>bannerClosed</td>\r\n            <td>boolean</td>\r\n            <td>false</td>\r\n            <td>false</td>\r\n            <td>Default state the banner is. Open by default.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>onToggle</td>\r\n            <td>function</td>\r\n            <td>null</td>\r\n            <td>false</td>\r\n            <td>Function which is called when you toggle the icon close.</td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Sale Banner</h2>\r\n      <DemoSection title=\"Sale Banner\" :code=\"demoCode\">\r\n        <template #components>\r\n          <banner-sale\r\n            title=\"Flash Sale! 25% off everything!\"\r\n            desc=\"All items are automatically discounted.\"\r\n            days=\"00\"\r\n            hours=\"00\"\r\n            minutes=\"00\"\r\n            :seconds=\"secs\"\r\n            time-desc=\"Until flash sale ends. Hurry!\"\r\n          />\r\n        </template>\r\n      </DemoSection>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Prop</th>\r\n            <th>Type</th>\r\n            <th>Required</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>title</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>null</td>\r\n            <td>Banner main title.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>desc</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>null</td>\r\n            <td>Banner description text below main title.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>days</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>\"00\"</td>\r\n            <td>Days remaining value for countdown timer</td>\r\n          </tr>\r\n          <tr>\r\n            <td>hours</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>\"00\"</td>\r\n            <td>Hours remaining value for countdown timer</td>\r\n          </tr>\r\n          <tr>\r\n            <td>minutes</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>\"00\"</td>\r\n            <td>Minutes remaining value for countdown timer</td>\r\n          </tr>\r\n          <tr>\r\n            <td>seconds</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>\"00\"</td>\r\n            <td>Seconds remaining value for countdown timer</td>\r\n          </tr>\r\n          <tr>\r\n            <td>timeDesc</td>\r\n            <td>string</td>\r\n            <td>false</td>\r\n            <td>null</td>\r\n            <td>Description text below countdown timer.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>borderColor</td>\r\n            <td>string</td>\r\n            <td>false</td>\r\n            <td>\"rgba(248, 86, 64, 0.33)\"</td>\r\n            <td>Banner border color.</td>\r\n          </tr>\r\n          <tr>\r\n            <td>backgroundColor</td>\r\n            <td>boolean</td>\r\n            <td>false</td>\r\n            <td>\"rgba(248, 86, 64, 0.08)\"</td>\r\n            <td>Banner background color.</td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Notice Banner</h2>\r\n      <DemoSection title=\"Notice Banner\" :code=\"demoCode\">\r\n        <template #components>\r\n          <Notice\r\n            title=\"\r\n                Join affiliates and earn $1 for each Streamlabs OBS referral\r\n              \"\r\n            desc=\"\r\n                Share your unique referral link with friends and get paid directly into your PayPal each month.\r\n              \"\r\n            icon=\"information\"\r\n          >\r\n            <Button\r\n              slot=\"button\"\r\n              type=\"button\"\r\n              size=\"fixed-width\"\r\n              variation=\"action\"\r\n              title=\"Join\"\r\n            />\r\n          </Notice>\r\n        </template>\r\n      </DemoSection>\r\n\r\n      <br />\r\n      <br />\r\n\r\n      <Notice\r\n        variation=\"warning\"\r\n        title=\"Your donation link has expired\"\r\n        desc=\"Copy your new donation link and replace all instances containing twitchalerts.com.\"\r\n      >\r\n        <Button\r\n          slot=\"button\"\r\n          type=\"button\"\r\n          size=\"fixed-width\"\r\n          variation=\"default\"\r\n          title=\"Copy Link\"\r\n        />\r\n      </Notice>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Prop</th>\r\n            <th>Type</th>\r\n            <th>Required</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>variation</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>default</td>\r\n            <td>\r\n              Variation style that will be displayed. Options are \"default\" and\r\n              \"warning\".\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>title</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>null</td>\r\n            <td>Banner title</td>\r\n          </tr>\r\n          <tr>\r\n            <td>desc</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>null</td>\r\n            <td>Description text below banner title</td>\r\n          </tr>\r\n          <tr>\r\n            <td>icon</td>\r\n            <td>string</td>\r\n            <td>true</td>\r\n            <td>null</td>\r\n            <td>\r\n              Icon next to title and in background. Will override selected\r\n              variation icon. Use icon name from icon list.\r\n            </td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Discord Banner</h2>\r\n      <DemoSection title=\"Discord Banner\" :code=\"demoCode\">\r\n        <template #components>\r\n          <BannerDiscord />\r\n        </template>\r\n      </DemoSection>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Prop</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>title</td>\r\n            <td>string</td>\r\n            <td>\r\n              Join the Streamlabs OBS Discussion on\r\n              &lt;span&gt;Discord&lt;/span&gt;\r\n            </td>\r\n            <td>\r\n              Set the title of the banner. You can wrap text in\r\n              <code>&lt;span&gt;</code> tags to give them medium font weight.\r\n            </td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Introduction Banner</h2>\r\n      <DemoSection title=\"Introduction Banner\" :code=\"demoCode\">\r\n        <template #components>\r\n          <BannerIntroduction bgColor=\"#5e3bec\">\r\n            <img\r\n              slot=\"bgImage\"\r\n              src=\"https://cdn.streamlabs.com/static/alert-box-sounds-banner-bg.png\"\r\n              alt=\"\"\r\n              class=\"banner-introduction-bgImage\"\r\n            />\r\n            <template #title\r\n              >Introducing<span class=\"banner-introduction-title\"\r\n                >Prime Alert Box Sounds</span\r\n              ></template\r\n            >\r\n            <template #description\r\n              >Modern, hype sounds you can add to your live alerts. All included\r\n              with Prime.</template\r\n            >\r\n            <Button\r\n              slot=\"button\"\r\n              title=\"Browse Sounds\"\r\n              :bgColor=\"'#000'\"\r\n              :textColor=\"'#fff'\"\r\n            ></Button>\r\n          </BannerIntroduction>\r\n        </template>\r\n      </DemoSection>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Prop</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>bgColor</td>\r\n            <td>string</td>\r\n            <td>Null</td>\r\n            <td>Set the background color of the banner.</td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\nimport BannersCode from \"!!raw-loader!./Banners.vue\";\r\nimport BannerDiscord from \"./../components/BannerDiscord.vue\";\r\nimport BannerIntroduction from \"./../components/BannerIntroduction.vue\";\r\nimport BannerMarketing from \"./../components/BannerMarketing.vue\";\r\nimport BannerSale from \"./../components/BannerSale.vue\";\r\nimport Button from \"./../components/Button.vue\";\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport Notice from \"./../components/Notice.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    BannerDiscord,\r\n    BannerIntroduction,\r\n    BannerMarketing,\r\n    BannerSale,\r\n    Button,\r\n    DemoSection,\r\n    Notice\r\n  }\r\n})\r\nexport default class Banners extends Vue {\r\n  demoCode = BannersCode;\r\n\r\n  remainingSecs = 10;\r\n\r\n  bannerClosed = false;\r\n\r\n  mounted() {\r\n    setInterval(() => {\r\n      this.remainingSecs--;\r\n      if (this.remainingSecs < 0) {\r\n        this.remainingSecs = 10;\r\n      }\r\n    }, 1000);\r\n  }\r\n\r\n  get secs() {\r\n    return this.remainingSecs < 10\r\n      ? `0${this.remainingSecs}`\r\n      : `${this.remainingSecs}`;\r\n  }\r\n\r\n  test() {\r\n    console.log(\"test\");\r\n  }\r\n}\r\n</script>\r\n\r\n<style lang=\"less\" scoped>\r\n.banner-introduction-title {\r\n  font-weight: 500;\r\n  padding: 0 8px;\r\n}\r\n\r\n.banner-introduction-bgImage {\r\n  position: absolute;\r\n  width: auto;\r\n  height: 100%;\r\n  right: 0px;\r\n  border-radius: 8px;\r\n}\r\n</style>\r\n";