export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Color Picker</h1>\r\n      <p>This color picker uses a chrome style picker</p>\r\n\r\n      <pre><code>import { ColorPicker } from \"streamlabs-beaker\"\r\n\r\ncomponents: {\r\n  ColorPicker\r\n}</code></pre>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Default Color Picker</h2>\r\n\r\n      <div class=\"section\">\r\n        <DemoSection title=\"Default\" :code=\"demoCode\">\r\n          <template #components>\r\n            <ColorPicker v-model=\"color\" />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>With Alpha</h3>\r\n        <DemoSection title=\"Alpha\" :code=\"demoCode\">\r\n          <template #components>\r\n            <ColorPicker\r\n              :value=\"alphaColor\"\r\n              :allColorTypes=\"true\"\r\n              :hasAlpha=\"true\"\r\n              @input=\"setAlphaColor\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Mini, with hex code selector</h3>\r\n        <DemoSection title=\"Mini\" :code=\"demoCode\">\r\n          <template #components>\r\n            <ColorPicker :isMini=\"true\" v-model=\"miniColor\" />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <div class=\"section\">\r\n        <h3>Mini, with icon (optional)</h3>\r\n        <DemoSection title=\"MiniIcon\" :code=\"demoCode\">\r\n          <template #components>\r\n            <ColorPicker\r\n              icon=\"icon-text\"\r\n              :isMini=\"true\"\r\n              v-model=\"miniIconColor\"\r\n            />\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Props</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>value</td>\r\n            <td>String</td>\r\n            <td>#31c3a2</td>\r\n            <td>Color to show up on a fresh color picker</td>\r\n          </tr>\r\n          <tr>\r\n            <td>placeholder</td>\r\n            <td>String</td>\r\n            <td>null</td>\r\n            <td>It shows as a placeholder in case of blank</td>\r\n          </tr>\r\n          <tr>\r\n            <td>hasAlpha</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>Option to add alpha setting for the color picker</td>\r\n          </tr>\r\n          <tr>\r\n            <td>input events</td>\r\n            <td>event</td>\r\n            <td>-</td>\r\n            <td>\r\n              Use any standard input field event and it will be tracked to the\r\n              input field\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>isMini</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>\r\n              Mini colorpicker, as just a square. Includes hex code in color\r\n              picker dropdown.\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>icon</td>\r\n            <td>String</td>\r\n            <td>null</td>\r\n            <td>\r\n              Icon to use with mini colorpicker.\r\n            </td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport ColorPicker from \"./../components/ColorPicker.vue\";\r\nimport ColorPickersCode from \"!!raw-loader!./ColorPickers.vue\";\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    ColorPicker,\r\n    DemoSection\r\n  }\r\n})\r\nexport default class ColorPickers extends Vue {\r\n  demoCode = ColorPickersCode;\r\n  color = \"#5E3BEC\";\r\n  alphaColor = \"#EB7777\";\r\n  miniColor = \"#5E3BEC\";\r\n  miniIconColor = \"#5E3BEC\";\r\n\r\n  setAlphaColor(color) {\r\n    this.alphaColor = color.hex;\r\n  }\r\n}\r\n</script>\r\n";