




















import { Component, Prop, Vue } from "vue-property-decorator";

@Component({})
export default class Notice extends Vue {
  @Prop({ default: "default" })
  variation!: string;

  @Prop({ required: true })
  title!: string;

  @Prop({ required: true })
  desc!: string;

  @Prop()
  icon!: string;

  get iconType() {
    switch (this.variation) {
      case "default":
        return "information";
      case "warning":
        return "error";
    }
  }

  get iconClass() {
    const iconSrc = this.icon ? this.icon : this.iconType;
    return `icon-${iconSrc}`;
  }
}
