export default "<template>\r\n  <div>\r\n    <h1>Tabs</h1>\r\n    <p>Used for Tabs.</p>\r\n\r\n    <pre><code>import { Tabs } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  Tabs\r\n}</code></pre>\r\n\r\n    <div class=\"section\">\r\n      <DemoSection title=\"Default\" :code=\"demoCode\">\r\n        <template #components>\r\n          <Tabs\r\n            :tabs=\"tabs\"\r\n            size=\"small\"\r\n            :update-route=\"false\"\r\n            selected=\"advanced\"\r\n          >\r\n            <div :slot=\"tab.value\" v-for=\"tab in tabs\" :key=\"tab.value\">\r\n              {{ tab.name }}\r\n            </div>\r\n          </Tabs>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <DemoSection title=\"New Tabs\" :code=\"demoCode\">\r\n        <template #components>\r\n          <TabsNew :tabs=\"tabs\" size=\"small\" selected=\"advanced\">\r\n            <div :slot=\"tab.value\" v-for=\"tab in tabs\" :key=\"tab.value\">\r\n              {{ tab.name }}\r\n            </div>\r\n          </TabsNew>\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <table class=\"docs-table\">\r\n      <thead>\r\n        <tr>\r\n          <th>Prop</th>\r\n          <th>Type</th>\r\n          <th>Default</th>\r\n          <th>Description</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr>\r\n          <td>tabs</td>\r\n          <td>array</td>\r\n          <td>null</td>\r\n          <td>\r\n            tabs information to display. you can make array like below\r\n            <br />\r\n            <code\r\n              >tabs = [ { name: \"General\", value: \"general\", icon: \"information\"\r\n              }, ... ];</code\r\n            >\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>size</td>\r\n          <td>string</td>\r\n          <td>small</td>\r\n          <td>text size of tabs. Options are \"small\" and \"large\"</td>\r\n        </tr>\r\n        <tr>\r\n          <td>selected</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>default selected tab</td>\r\n        </tr>\r\n        <tr>\r\n          <td>updateRoute</td>\r\n          <td>boolean</td>\r\n          <td>true</td>\r\n          <td>\r\n            Updates current URL with \"#/{selected Tab}\". Set to false if you\r\n            don't want to update the URL.\r\n          </td>\r\n        </tr>\r\n      </tbody>\r\n    </table>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport ScrollNav from \"./../components/ScrollNav.vue\";\r\nimport Tabs from \"./../components/Tabs.vue\";\r\nimport TabsNew from \"./../components/TabsNew.vue\";\r\nimport TabsCode from \"!!raw-loader!./Tabs.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    ScrollNav,\r\n    Tabs,\r\n    TabsNew\r\n  }\r\n})\r\nexport default class TabsDemo extends Vue {\r\n  demoCode = TabsCode;\r\n  tabs = [\r\n    {\r\n      name: \"General\",\r\n      value: \"general\",\r\n      icon: \"information\"\r\n    },\r\n    {\r\n      name: \"Advanced\",\r\n      value: \"advanced\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Account\",\r\n      value: \"account\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Integrations\",\r\n      value: \"integrations\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Payments\",\r\n      value: \"payments\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Donations\",\r\n      value: \"donations\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Subscriptions\",\r\n      value: \"subscriptions\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Preferences\",\r\n      value: \"preferences\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Apps\",\r\n      value: \"apps\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Merch\",\r\n      value: \"merch\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"API\",\r\n      value: \"api\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Moderators\",\r\n      value: \"moderators\",\r\n      icon: \"\"\r\n    },\r\n    {\r\n      name: \"Themes\",\r\n      value: \"themes\",\r\n      icon: \"\"\r\n    }\r\n  ];\r\n}\r\n</script>\r\n";