export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Step</h1>\r\n      <p>step component</p>\r\n\r\n      <pre><code>import { Step } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  Step\r\n}</code></pre>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <div class=\"row\">\r\n        <DemoSection title=\"Default\" :code=\"demoCode\">\r\n          <template #components>\r\n            <FormGroup>\r\n              <Step slot=\"input\" title=\"Enable Pro\">\r\n                <div class=\"info\">\r\n                  <p>+20 CCV</p>\r\n                  <i class=\"icon-information\"></i>\r\n                </div>\r\n              </Step>\r\n\r\n              <Step\r\n                slot=\"input\"\r\n                title=\"Enable Pro\"\r\n                :isCompleted=\"true\"\r\n                :hasCheckmark=\"true\"\r\n                completedText=\"Complete\"\r\n              >\r\n                <div class=\"info\">\r\n                  <p>+20 CCV</p>\r\n                  <i class=\"icon-information\"></i>\r\n                </div>\r\n              </Step>\r\n\r\n              <Step\r\n                slot=\"input\"\r\n                icon=\"icon-donation-settings\"\r\n                title=\"Tip\"\r\n                :hasPrime=\"true\"\r\n                >0/1</Step\r\n              >\r\n\r\n              <Step\r\n                slot=\"input\"\r\n                icon=\"icon-donation-settings\"\r\n                title=\"Tip\"\r\n                :isCompleted=\"true\"\r\n                completedText=\"1/1\"\r\n                >0/1</Step\r\n              >\r\n            </FormGroup>\r\n          </template>\r\n        </DemoSection>\r\n      </div>\r\n\r\n      <table class=\"docs-table\">\r\n        <thead>\r\n          <tr>\r\n            <th>Props</th>\r\n            <th>Type</th>\r\n            <th>Default</th>\r\n            <th>Description</th>\r\n          </tr>\r\n        </thead>\r\n        <tbody>\r\n          <tr>\r\n            <td>title</td>\r\n            <td>String</td>\r\n            <td>null</td>\r\n            <td>title to display</td>\r\n          </tr>\r\n          <tr>\r\n            <td>icon</td>\r\n            <td>String</td>\r\n            <td>null</td>\r\n            <td>icon to display</td>\r\n          </tr>\r\n          <tr>\r\n            <td>isCompleted</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>\r\n              background would be @dark-4, color would be @dark-5 when it is\r\n              true\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>completedText</td>\r\n            <td>String</td>\r\n            <td>null</td>\r\n            <td>\r\n              It shows instead of slot when isCompleted is true\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>isChecked</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>\r\n              add check mark icon and line through for title when it is true\r\n            </td>\r\n          </tr>\r\n          <tr>\r\n            <td>hasPrime</td>\r\n            <td>Boolean</td>\r\n            <td>false</td>\r\n            <td>It shows prime alt badge when it is true</td>\r\n          </tr>\r\n        </tbody>\r\n      </table>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport FormGroup from \"./../components/FormGroup.vue\";\r\nimport Step from \"./../components/Step.vue\";\r\nimport StepsCode from \"!!raw-loader!./Steps.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    FormGroup,\r\n    Step\r\n  }\r\n})\r\nexport default class Steps extends Vue {\r\n  demoCode = StepsCode;\r\n}\r\n</script>\r\n\r\n<style lang=\"less\" scoped>\r\n@import (reference) \"./../styles/Imports\";\r\n\r\n.info {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n\r\n  p {\r\n    .margin(0);\r\n    margin-right: 15px;\r\n    color: @dark-5;\r\n    background: @light-blue;\r\n    .weight(@medium);\r\n    .radius();\r\n    .padding();\r\n  }\r\n}\r\n</style>\r\n";