
























import { Component, Prop, Vue } from "vue-property-decorator";
import Badge from "./../components/Badge.vue";

@Component({
  components: {
    Badge
  }
})
export default class Callout extends Vue {
  @Prop({ default: "success" })
  variation!: string;

  @Prop({ default: true })
  icon!: boolean;

  @Prop()
  customIcon!: string;

  @Prop({ default: false })
  closeable!: boolean;

  @Prop()
  onClose!: Function;

  closed: boolean = false;
  calloutClosedClass: string = "";

  closeCallout() {
    this.calloutClosedClass = "callout--closed";
    setTimeout(() => {
      typeof this.onClose === "function" && this.onClose();
    }, 275);
  }

  get calloutClass() {
    return `s-callout--${this.variation}`;
  }

  get calloutIcon() {
    if (this.customIcon) return this.customIcon;
    switch (this.variation) {
      case "success":
      case "success-alt":
        return "icon-check";
      case "warning":
      case "warning-alt":
        return "icon-error";
      case "info":
        return "icon-information";
      case "cookies":
        return "icon-information";
    }
  }

  get isPrime() {
    return this.variation === "prime";
  }
}
