export default "<template>\r\n  <div>\r\n    <div class=\"section\">\r\n      <h1>Toggles</h1>\r\n      <p>Toggle between multiple custom options such as show and hide.</p>\r\n\r\n      <pre><code>import { Toggle } from 'streamlabs-beaker';\r\n\r\ncomponents: {\r\n  Toggle\r\n}</code></pre>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Icon Toggles</h2>\r\n      <DemoSection title=\"Icon Toggles\" :code=\"demoCode\">\r\n        <template #components>\r\n          <Toggle\r\n            :values=\"{\r\n              show: `<i class='icon-view'></i>`,\r\n              hide: `<i class='icon-hide'></i>`\r\n            }\"\r\n            v-model=\"selectedOption\"\r\n          />\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <div class=\"section\">\r\n      <h2>Text Toggles</h2>\r\n      <DemoSection title=\"Text Toggles\" :code=\"demoCode\">\r\n        <template #components>\r\n          <Toggle\r\n            :values=\"{\r\n              revenue: 'Revenue',\r\n              growth: 'Growth'\r\n            }\"\r\n            v-model=\"selectedTextOption\"\r\n            :variation=\"'text'\"\r\n          />\r\n        </template>\r\n      </DemoSection>\r\n    </div>\r\n\r\n    <table class=\"docs-table\">\r\n      <thead>\r\n        <tr>\r\n          <th>Prop</th>\r\n          <th>Type</th>\r\n          <th>Default</th>\r\n          <th>Description</th>\r\n        </tr>\r\n      </thead>\r\n      <tbody>\r\n        <tr>\r\n          <td>values</td>\r\n          <td>object</td>\r\n          <td>null</td>\r\n          <td>\r\n            Set toggle options with strings for the keys and HTML for the\r\n            values. Keys will also be displayed as toggle title attributes.\r\n          </td>\r\n        </tr>\r\n        <tr>\r\n          <td>v-model</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>Use as a bind to the currently selected toggle option.</td>\r\n        </tr>\r\n        <tr>\r\n          <td>variation</td>\r\n          <td>string</td>\r\n          <td>null</td>\r\n          <td>\r\n            Add a toggle variation for a subtle style change. Options are\r\n            <code>text</code>.\r\n          </td>\r\n        </tr>\r\n      </tbody>\r\n    </table>\r\n  </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\nimport { Component, Vue } from \"vue-property-decorator\";\r\n\r\nimport DemoSection from \"./../components/DemoSection.vue\";\r\nimport Toggle from \"./../components/Toggle.vue\";\r\nimport TogglesCode from \"!!raw-loader!./Toggles.vue\";\r\n\r\n@Component({\r\n  components: {\r\n    DemoSection,\r\n    Toggle\r\n  }\r\n})\r\nexport default class Toggles extends Vue {\r\n  demoCode = TogglesCode;\r\n  selectedOption = \"show\";\r\n  selectedTextOption = \"revenue\";\r\n}\r\n</script>\r\n";